/* Global layout */
body, html, .App {
    margin: 0;
    padding: 0;
    background-color: #000;
    color: #fff;
    font-family: Arial, sans-serif;
    text-align: center;
}

/* Header */
.header {
    padding: 2rem 1rem;
    border-bottom: 1px solid #444;
}

.profile-pic {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 1rem;
}

.header h1 {
    margin: 0.5rem 0;
    font-size: 2rem;
}

.header nav a {
    color: #9c6bff;
    margin: 0 1rem;
    text-decoration: none;
    font-weight: bold;
}

    .header nav a:hover {
        text-decoration: underline;
    }

/* Sections */
section {
    padding: 2rem 1rem;
}

.project-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    margin-top: 1rem;
}

.project-card {
    background-color: #111;
    padding: 1rem;
    border: 1px solid #333;
    border-radius: 8px;
}

    .project-card a {
        display: inline-block;
        margin: 0.25rem;
        color: #9c6bff;
        text-decoration: none;
    }

        .project-card a:hover {
            text-decoration: underline;
        }

/* Footer */
.footer {
    padding: 1rem;
    background-color: #111;
    border-top: 1px solid #444;
    font-size: 0.9rem;
}